import Image from "next/image";
import Link from "next/link";
import { useState } from "react";
import { Eye, EyeOff } from "lucide-react";

export default function Home() {
  const [showPassword, setShowPassword] = useState(false);

  return (
    <div className="grid grid-rows-[0px_1fr_20px] items-center justify-items-center min-h-screen sm:pt-[64px] pt-[30px] gap-16 font-[family-name:var(--font-geist-sans)]">
      <Image src="/Logo.svg" alt="Dating logo" width={48} height={88} />
      <main className="bg-[#F9F9F9] p-12 row-start-2 items-center max-w-[600px] text-center">
        <div className="mb-10 sm:pt-0 pt-20 flex flex-col gap-[32px]">
          <h3 className="text-[24px] leading-[32px] font-semibold mb-8">
            Registration
          </h3>
          <div className="w-full">
            <input
              placeholder="Full name"
              type="name"
              id="name"
              className="border-1 border-[#D9D9D9] bg-[#fff] px-[12px] py-[9px] h-[40px] w-full"
            />
          </div>
          <div className="">
            <input
              placeholder="Email"
              type="email"
              id="email"
              className="border-1 border-[#D9D9D9] bg-[#fff] px-[12px] py-[9px] h-[40px] w-full"
            />
          </div>
          <div className="">
            <input
              placeholder="Phone"
              type="tel"
              id="phone"
              pattern="[0-9]{10,15}"
              className="border-1 border-[#D9D9D9] bg-[#fff] px-[12px] py-[9px] h-[40px] w-full"
            />
          </div>
          <input
            placeholder="Password"
            type={showPassword ? "text" : "password"}
            id="password"
            className="border-1 border-[#D9D9D9] bg-white px-[12px] pr-[40px] py-[9px] h-[40px] w-full"
          />
          <button
            type="button"
            onClick={() => setShowPassword(!showPassword)}
            className="absolute right-3 top-1/2 -translate-y-1/2 text-gray-500 hover:text-black">
            {showPassword ? <EyeOff size={18} /> : <Eye size={18} />}
          </button>
        </div>
        <div className="flex flex-wrap md:gap-4 gap-2">
          <Link
            href=""
            className="bg-[#1677FF] text-[#fff] rounded-[10px] text-base py-[9px] px-[16px] sm:w-auto w-full  leading-6 h-10">
            Iâ€™m over 18 years old
          </Link>
          <Link
            href=""
            className="border-1 rounded-[10px] border-[#D9D9D9] text-base py-[8px] px-[16px] sm:w-auto w-full leading-6 h-10">
            Lrave the service
          </Link>
        </div>
      </main>
    </div>
  );
}
